.container-white {
    background-color: rgba(255, 255, 255, 0.4);
    backdrop-filter: blur(20px);
    border-radius: 10px;
    width: 60%;
    margin: 25px auto;
    border: 2px solid white;
    border-radius: 10px;
}

@media (min-width: 600px) {
    .container-white {
        width: 80%;
    }
}